<template>

  <section class="section">
    <div class="container">
      <div class='holder' v-on:click.stop="toggleEditMode(null, $event)">
        <router-link :to="{name: 'Category', params: {id: this.category_id}}">
          <div class="title"><span class="fa fa-arrow-left "></span> {{this.$store.state.selectedCategory.name}}</div>
        </router-link>

        <ul>

          <add_card v-if="flash_cards.length > 9" v-bind:flash_cards_length="counter"></add_card>

          <li v-on:click.stop="toggleEditMode(index, $event)"
              v-bind:class="{inEdit: editModeForIndex(index)}"
              v-for="(card, index) in this.flash_cards">
            <div class="face">
              <div v-show="!editModeForIndex(index)" data-edit="true" class="editControl edit">
                <i class="fa fa-trash-o" data-edit="true"></i>
              </div>
              <div v-show="editModeForIndex(index)" v-on:click='deleteCard(card.id)' class="editControl close"><i
                class="fa fa-times"></i></div>
              <div class="cardNumber">{{index + 1}}</div>
              <div>
                {{card.face}}
              </div>
            </div>
            <div class="back">
              <div>
                {{card.back}}
              </div>
            </div>
          </li>

          <add_card v-bind:flash_cards_length="counter"></add_card>

        </ul>

      </div>
    </div>
    <div class="fixed-button">
      <div class="button" v-on:click="sortCards()">
        <span class="icon is-small">
          <i v-bind:class='["fa", `fa-sort${sortedCards}`]'></i>
        </span>
        <span>A-Z</span>
      </div>
    </div>
  </section>


</template>

<script>
  import AddCard from "./AddCard.vue";

  export default {
    components: {
      'add_card': AddCard
    },
    name: 'Cards',
    props: ['category_id'],
    data: function () {
      return {
        faceInput: '',
        backInput: '',
        selectedIndex: null
      }
    },
    computed: {
      flash_cards: function () {
        return this.$store.state.selectedCategory.flash_cards
      },
      addNewMode: function () {
        return this.$store.state.isInEdit;
      },
      counter: function () {
        return this.flash_cards.length
      },
      sortedCards: function () {
        return this.$store.state.selectedCategory.sorted
      }
    },
    methods: {
      enterNewMode: function () {
        if (this.addNewMode) {
          return
        }
        this.faceInput = "";
        this.backInput = "";

        this.$store.commit('setInEdit');
      },

      addCard: function () {
        this.$store.dispatch('createCard', {face: this.faceInput, back: this.backInput, router: this.$router})
      },

      toggleEditMode: function (selectedIndex, event) {
        if (event.target.hasAttribute('data-edit')) {
          this.selectedIndex = selectedIndex
        } else {
          if (selectedIndex === null || event.target.closest('li').className !== 'inEdit') {
            this.selectedIndex = null
          }
        }
      },

      editModeForIndex: function (index) {
        return index === this.selectedIndex
      },

      deleteCard: function (cardId) {
        this.selectedIndex = null;
        this.$store.dispatch('deleteCard', {id: cardId, router: this.$router})
      },

      sortCards: function () {
        this.$store.dispatch('sortCards', {router: this.$router})
      }
    },
    created: function () {
      if (this.category_id !== this.$store.state.selectedCategory.category_id ||
        this.$store.state.selectedCategory.category_id == null) {
        this.$store.dispatch('selectCategory', {id: this.category_id, router: this.$router})
      }
    }
  }

</script>

<style lang="sass" scoped>
  @import "../values.sass"
  @keyframes waggle
    0%
      transform: rotateZ(0deg) scale(1.01)
    10%
      transform: rotateZ(-1deg) scale(1.01)
    20%
      transform: rotateZ(1deg) scale(1.01)
    27.5%
      transform: rotateZ(-1deg) scale(1.01)
    35%
      transform: rotateZ(1deg) scale(1.01)
    52.5%
      transform: rotateZ(-1deg) scale(1.01)
    75%
      transform: rotateZ(1deg) scale(1.01)
    90%
      transform: rotateZ(-1deg) scale(1.01)
    100%
      transform: rotateZ(1deg) scale(1.01)

  .holder
    margin: 2 * $margin auto
    max-width: 1000px
    width: 100%
    box-sizing: border-box
    position: relative
    text-align: center

  +mobile
    .section
      padding-top: 0
    .holder
      margin: 0 auto

  .title
    color: $gray
    padding: $margin
    display: inline-block
    @include medium_bold_font
    &:hover
      color: $main_color_dark

  ul
    list-style: none
    width: 100%
    padding: 0
    display: flex
    align-items: flex-start
    justify-content: space-around
    flex-wrap: wrap

  li
    display: flex
    flex-basis: 300px
    box-sizing: border-box
    min-height: 120px
    width: 30%
    padding: 0
    margin-bottom: $margin
    border-radius: 10px
    background: white
    @include defaultShadow
    color: black
    text-align: center
    transition: all .1s ease-in-out
    flex-direction: column
    overflow: hidden
    position: relative
    &.inEdit
      border: 1px dashed $red
      animation: waggle 2s infinite

    .cardNumber
      text-align: center
      font: 300 0.75em 'Montserrat', 'Open Sans', sans-serif
      position: absolute
      top: 10px
      left: 10px
      padding: 0 !important
      color: $main_color_dark

    .editControl
      top: 0px
      right: 0
      font: 300 1em 'Montserrat', 'Open Sans', sans-serif
      cursor: pointer
      position: absolute
      text-shadow: 0px 1px 0px lighten($light_gray, 30%)
      padding: 10px

      &.close
        color: $red_dark
      &.edit
        color: $light_gray

    .face
      z-index: 2
      @include small_bold_font
      @include defaultShadow
    .back
      @include small_default_font
      background-color: $yellow

    .face, .back
      flex: 1
      display: flex
      flex-direction: column
      div
        padding: 10px
        margin: auto

  .fixed-button
    +tablet
      right: $margin

    +mobile
      right: $margin
    +desktop
      right: $margin * 5
    position: fixed
    z-index: 3
    top: $margin * 5
    .button
      width: 60px
      height: 60px
      border-radius: 50%
      color: white
      background-color: $light_gray

</style>



