<template>
  <section class="section category">
    <div class="container">

      <stars v-if="sessionStarted" v-bind:clickable="isReturnToCategory"></stars>
      <router-link  v-if="!sessionStarted" :to="{name: 'Categories'}">
        <div class="title"><span class="fa fa-arrow-left "></span> Categories</div>
      </router-link>

      <div class="columns">

        <div class="column is-half is-offset-one-quarter" v-if="sessionStarted">

          <card v-bind:id="card.id"
                v-bind:isFlipped="cardIsFlipped"
                v-bind:face="card.face"
                v-bind:back="card.back"
                v-bind:box="card.box"
                v-bind:index="card.index"
          ></card>

          <div class="is-flex buttonHolder">
            <div class="button is-blue" v-if="!isReturnToCategory" v-on:click="flipCard">I Don't Know</div>
            <div class="button is-primary" v-if="!isReturnToCategory && lastCard" v-on:click.once="finishSession">Finish</div>
            <div class="button is-primary" v-if="!isReturnToCategory && !lastCard" v-on:click="nextCard">Next</div>
            <div class="button is-primary only" v-if="isReturnToCategory" v-on:click="returnToCategory">Back to: {{categoryName}}</div>
          </div>

          <div class="">
            <a class="button is-text" v-on:click="cancelSession" v-if="sessionStarted && !isReturnToCategory">Finish Now</a>
          </div>


        </div>

        <div class="column categoryInfoHolder is-narrow" v-else>
          <category-info v-bind:id="id"></category-info>
        </div>

        <finish-dialog v-if="showFinishDialog"></finish-dialog>
      </div>
    </div>
  </section>
</template>

<script>
  import CategoryInfo from "./CategoryInfo.vue";
  import Card from "./Card.vue";
  import FinishDialog from "./FinishDialog.vue";
  import Stars from "./Stars.vue";

  export default {
    name: 'Category',

    components: {
      'category-info': CategoryInfo,
      'card': Card,
      'finish-dialog': FinishDialog,
      'stars': Stars
    },

    data: function () {
      return {
        cardIsFlipped: false
      }
    },

    props: {
      id: String
    },

    computed: {
      categoryName: function () {
        return this.$store.state.selectedCategory.name
      },
      sessionStarted: function () {
        return this.$store.state.session.started
      },
      lastCard: function () {
        return this.$store.getters.isLastQuestion;
      },
      card: function () {
        let currentQuestion = this.$store.state.session.currentQuestion;

        return this.$store.state.session.questions[currentQuestion];
      },
      showFinishDialog: function () {
        return this.$store.state.session.showFinishDialog;
      },

      isReturnToCategory: function () {
        return this.$store.state.session.showFinishDialog === false &&
          this.$store.state.session.isFinished === true &&
          this.$store.state.session.started === true
      }
    },
    methods: {
      flipCard: function () {
        this.cardIsFlipped = !this.cardIsFlipped;
        this.$store.dispatch('dontKnow', {router: this.$router});
      },
      nextCard: function () {
        this.cardIsFlipped = false;
        this.$store.dispatch('nextCard', {router: this.$router});
      },
      finishSession: function () {
        this.cardIsFlipped = false;
        this.$store.dispatch('finishSession', {router: this.$router});
      },
      returnToCategory: function () {
        this.$store.dispatch('returnToCategory', {router: this.$router});
      },
      cancelSession: function(){
        this.cardIsFlipped = false;
        this.$store.dispatch('cancelSession', {router: this.$router});
      }
    }
  }
</script>

<style lang="sass" scoped>
  @import "../values.sass"
  .category
    text-align: center
    .title
      color: $gray
      padding: $margin
      margin-top: $margin * 2
      display: inline-block
      @include medium_bold_font
      &:hover
        color: $main_color_dark

  .categoryInfoHolder
    margin: auto

  .buttonHolder .button:first-child
    margin-right: 1.5rem

  .buttonHolder .button.only
    margin: 0

  .buttonHolder
    margin-top: 1.5rem
    margin-bottom: 1.5rem
    justify-content: space-around
    .button
      flex: 1
      height: 4rem

  +mobile
    .category
      padding-top: 1.5em
      .title
        margin-top: 0
        padding-top: 0
    .categoryInfoHolder
      margin-top: 0px

</style>



