<template>
  <li v-bind:class="['add-new', {'inactive': !addNewMode}]"
      v-on:click="enterNewMode">
    <div class="is-centered" v-if="!addNewMode">
      <span class="is-size-4 fa fa-plus-square-o"></span>
    </div>

    <Loading v-show="isInProgress" class="spinner" />

    <div v-if="addNewMode" class="face">
      <div class="cardNumber">{{this.flash_cards_length + 1}}</div>
      <div class="inputs">
        <div class="field">
          <div class="control">
            <input class="input" v-model="faceInput" type="text" placeholder="Face"/>
          </div>
        </div>
      </div>
    </div>

    <div v-if="addNewMode" class="back">
      <div class="inputs">
        <div class="field">
          <div class="control">
            <input v-on:keypress.enter="addCard" class="input" v-model="backInput" type="text" placeholder="Back"/>
          </div>
        </div>
      </div>

      <div v-if="addNewMode" class="controls">
        <a v-on:click.stop="addCard"><span class="is-size-5 fa fa-check check"></span></a>
        <a v-on:click.stop="exitNewMode"><span class="is-size-5 fa fa-remove cancel"></span></a>
      </div>
    </div>
  </li>
</template>

<script>
  import Loading from "./Loading.vue";
  export default {
    name: 'AddCard',
    components: {
      'Loading': Loading
    },
    props: ['flash_cards_length'],
    data: function () {
      return {
        faceInput: '',
        backInput: '',
        selectedIndex: null
      }
    },
    computed: {
      isInProgress: function () {
        return this.$store.state.isInProgress;
      },
      addNewMode: function () {
        return this.$store.state.isInEdit;
      }
    },
    methods: {
      enterNewMode: function () {
        if (this.addNewMode) {
          return
        }
        this.faceInput = "";
        this.backInput = "";

        this.$store.commit('setInEdit');
      },
      addCard: function () {
        this.$store.dispatch('createCard', {face: this.faceInput, back: this.backInput, router: this.$router})
      },
      exitNewMode: function () {
        this.$store.commit('removeInEdit');
      }
    }
  }

</script>

<style lang="sass" scoped>
  @import "../values.sass"
  li
    &.add-new
      position: relative
      .controls
        padding-top: 0
        flex: 1
        a
          margin: 0 20px
          span
            text-shadow: 0px -1px 0px darken($yellow, 30%), 0px 1px 0px lighten($yellow, 10%)
          .check
            color: $main_color_dark
            &:hover
              color: $main_color_light
          .cancel
            color: $red_dark
            &:hover
              color: $red

      .spinner
        top: 35px
        left: 130px

    &.add-new.inactive
      background-color: transparent
      box-shadow: 0px 0px 0px
      border: 3px dashed $light_gray
      color: $light_gray
      position: relative
      &:hover
        transform: scale(1.05)
      div
        margin: auto

    .cardNumber
      text-align: center
      font: 300 0.75em 'Montserrat', 'Open Sans', sans-serif
      position: absolute
      top: 10px
      left: 10px
      padding: 0 !important
      color: $main_color_dark

    .face
      z-index: 2
      @include small_bold_font
      @include defaultShadow
    .back
      @include small_default_font
      background-color: $yellow

    .face, .back
      flex: 1
      display: flex
      flex-direction: column
      div
        padding: 10px
        margin: auto

</style>



