<template>
  <div class="card">
    <div class="card-content">
      <h1 class="title">
        {{this.name}}
      </h1>
      <div class="content">
        <div class="columns is-mobile">

          <Loading v-show="isInProgress" class="spinner" />

          <div class="column">
            <router-link
              v-bind:to="{name: 'Cards', params: {category_id: this.categoryId}}"
              :event="this.isInProgress ? '' : 'click'"
              class="is-flex button cards">
              <div class="add-card is-warning button"><span class="fa fa-plus"></span><span>Add Card</span></div>
              <div>Total</div>
              <cards-svg :text="totalCards" width="60" height="60"></cards-svg>
            </router-link>

            <div class="is-flex cards">
              <div>Total points</div>
              <div class="is-bold">{{totalPoints}}</div>
            </div>
          </div>

          <div class="column">
            <a class="is-flex button cards">
              <div>Known</div>
              <cards-svg :text="0" width="60" height="60"></cards-svg>
            </a>
            <div class="is-flex cards">
              <div>Last points</div>
              <div class="is-bold">{{lastSessionPoints}}</div>
            </div>
          </div>

        </div>

        <div class="columns">
          <div class="column buttonHolder">
            <div v-if="totalCards > 0" class="button is-primary" v-on:click.once="startSession">Let's Start</div>
            <div v-else>
              <p>There are no cards yet. Let's add some</p>
              <router-link class="button is-primary" v-if="this.categoryId" v-bind:to="{name: 'Cards', params: {category_id: this.categoryId}}">
                Add Card
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import CardsSvg from "./svg/CardsSvg.vue";
  import Loading from "./Loading.vue";

  export default {
    name: 'CategoryInfo',

    components: {
      'cards-svg': CardsSvg,
      'Loading': Loading
    },

    props: {
      id: String
    },

    methods: {
      startSession: function () {
        this.$store.dispatch('startSession', {router: this.$router});
      }
    },

    computed: {
      totalCards: function () {
        return this.$store.state.selectedCategory.flash_cards.length
      },

      name: function () {
        return this.$store.state.selectedCategory.name
      },
      categoryId: function () {
        return this.$store.state.selectedCategory.category_id
      },

      totalPoints: function () {
        return this.$store.state.selectedCategory.total_points
      },

      lastSessionPoints: function () {
        return this.$store.state.selectedCategory.last_session_points
      },

      isInProgress: function () {
        return this.$store.state.isInProgress;
      }
    },

    created() {
      this.$store.dispatch('selectCategory', {id: this.id, router: this.$router});
    }
  }
</script>
<style lang="sass" scoped>
  @import "../values.sass"
  .card
    @include small_default_font
    .spinner
      top: 45%
      left: 45%

  .title
    @include medium_bold_font

  .is-flex
    &.cards
      min-height: 100px
      padding: 10px
      flex-wrap: wrap
      position: relative
      .add-card
        position: absolute
        border: 0px solid $light_gray
        right: 0
        bottom: 0
        &.button
          height: 20px
          font-size: small
          &:hover
            transform: scale(1)
    div
      margin: auto 0
      padding: 0 10px
      text-align: center
      width: 100%

  .buttonHolder
    display: flex
    text-align: center
    div
      margin: auto
    .button
      cursor: pointer
      margin: auto
      height: 3rem
      padding: 0 3em
</style>



