import Vue from 'vue';
import Vuex from 'vuex';

import ApiClient from '../services/ApiClient';
import LocalStorageService from '../services/LocalStorageService';
import removeByKey from '../services/Remove';

Vue.use(Vuex);

function loadKeys() {
  return {
    isInProgress: false,
    isInEdit: false,
    categories: [],
    selectedCategory: {
      category_id: null,
      name: '',
      flash_cards: [],
      sorted: '', // asc, desc
      is_default: false,
      total_points: 0,
      last_session_points: 0
    },
    session: {
      started: false,
      isFinished: false,
      showFinishDialog: false,
      currentQuestion: null,
      details: {},
      questions: []
    },
    auth: {
      isLoggedIn: false,
      token: null,
      expires_at: null
    },
    user: {
      name: null,
      email: null
    },
    errors: {}
  }
}

export const store = new Vuex.Store({
  strict: true,

  state: loadKeys(),

  getters: {
    isLastQuestion: function (state, getters) {
      return state.session.currentQuestion + 1 === state.session.questions.length;
    },

    score: function (state, getters) {
      if (!state.session.isFinished) {
        return 0;
      } else {
        let score = state.session.questions.reduce(function reduceQuestions(acc, curr) {
          if (curr.state === 'correct') {
            return acc + 1
          } else {
            return acc
          }
        }, 0);

        return score;
      }
    },

    stars: function (state, getters) {
      return state.session.questions.map(function (question) {
        return question.state
      })
    },

    authHeader: function (state) {
      return {'Authorization': `Bearer ${state.auth.token}`}
    }
  },

  mutations: {
    initialiseStore(state) {
      // Check if the ID exists
      if (localStorage.getItem('store')) {
        this.replaceState(
          Object.assign(state, JSON.parse(localStorage.getItem('store')))
        );
      }
    },
    selectCategory: (state, payload) => {
      state.selectedCategory.category_id = payload.id;
      state.selectedCategory.flash_cards = payload.flash_cards;
      state.selectedCategory.name = payload.name;
      state.selectedCategory.is_default = payload.is_default;
      state.selectedCategory.total_points = payload.total_points;
      state.selectedCategory.last_session_points = payload.last_session_points;
      state.session.started = false;
      state.session.showFinishDialog = false;
      state.session.isFinished = false;
      state.session.currentQuestion = null;
      state.session.questions = [];
    },

    clearCategory: (state, payload) => {
      state.selectedCategory.category_id = null;
      state.selectedCategory.flash_cards = [];
      state.selectedCategory.name = '';
      state.selectedCategory.is_default = false;
      state.selectedCategory.total_points = 0;
      state.selectedCategory.last_session_points = 0;
      state.session.started = false;
      state.session.showFinishDialog = false;
      state.session.isFinished = false;
      state.session.currentQuestion = null;
      state.session.questions = [];
    },

    startSession: (state, payload) => {
      state.session.started = true;
      state.session.currentQuestion = 0;
      state.session.details = payload.learning_session_details;
      state.session.questions = payload.cards
        .map((card, index) => {
          return {
            id: card.id,
            state: 'none',
            back: card.back,
            face: card.face,
            box: card.box,
            index: index + 1
          }
        });
    },

    dontKnow: function (state, payload) {
      state.session.questions[state.session.currentQuestion].state = 'incorrect';
    },

    nextCard: function (state, payload) {
      if (state.session.questions[state.session.currentQuestion].state === 'none') {
        state.session.questions[state.session.currentQuestion].state = 'correct'
      }

      state.session.currentQuestion += 1;
    },

    clickStar: (state, payload) => {
      state.session.currentQuestion = payload.index
    },

    closeFinishDialog: function (state, payload) {
      state.session.showFinishDialog = false;
    },

    finishSession: function (state, payload) {
      if (state.session.questions[state.session.currentQuestion].state == 'none') {
        state.session.questions[state.session.currentQuestion].state = 'correct';
      } else {
        state.session.questions[state.session.currentQuestion].state = 'incorrect';
      }
      state.session.isFinished = true;
      state.session.showFinishDialog = true;
    },

    returnToCategory: function (state, payload) {
      state.session.started = false;
      state.session.isFinished = true;
      state.session.showFinishDialog = false;
    },

    login: function (state, payload) {
      state.auth.isLoggedIn = true;
      state.auth.token = payload.json.token.token;
      state.auth.expires_at = payload.json.token.expires_at;
      state.user = payload.json.user;

      new LocalStorageService(state).setStore();
      payload.router.push({name: 'Home'});
    },

    signout: function (state, payload) {
      Object.assign(state, loadKeys());

      new LocalStorageService(state).wipeStore();
      payload.router.push({name: 'Login'})
    },

    fillErrors: function (state, payload) {
      if (payload.errors) {
        state.errors = payload.errors[0]
      }
    },

    cleanErrorKey: function (state, payload) {
      delete state.errors[payload.key];
    },

    setInProgress: function (state, payload) {
      state.isInProgress = true;
    },

    removeInProgress: function (state, payload) {
      state.isInProgress = false;
    },

    setInEdit: function (state, payload) {
      state.isInEdit = true;
    },

    removeInEdit: function (state, payload) {
      state.isInEdit = false;
    },

    setCategories: function (state, payload) {
      state.categories = payload;
    },

    addCategories: function (state, payload) {
      state.categories.push(payload)
    },

    addCard: function (state, payload) {
      state.selectedCategory.flash_cards.push(payload)
    },

    deleteCard: function (state, payload) {
      removeByKey(state.selectedCategory.flash_cards, {
        key: 'id',
        value: payload
      })
    },

    sortCards: function (state, payload) {
      var cards = state.selectedCategory.flash_cards
      let sorted = state.selectedCategory.sorted
      if (sorted == '') {
        cards = cards.sort(function (card1, card2) {
          let result = card1.face >= card2.face ? 1 : -1
          return result
        })
        sorted = '-asc'
      } else if (sorted == '-asc') {
        cards = cards.reverse()
        sorted = '-desc'
      } else {
        cards = cards.sort(function (card1, card2) {
          let result = card1.id >= card2.id ? 1 : -1
          return result
        })
        sorted = ''
      }
      state.selectedCategory.flash_cards = cards
      state.selectedCategory.sorted = sorted
    }

  },

  actions: {
    cleanErrorKey: (context, payload) => {
      context.commit('cleanErrorKey', payload)
    },

    signup: (context, payload) => {
      context.commit('setInProgress');

      ApiClient('/registrations.json',
        context,
        payload.router,
        {
          method: 'POST',
          body:
            JSON.stringify({
              registration:
                {
                  email: payload.body.email,
                  name: payload.body.name,
                  password: payload.body.password,
                }
            })
        },
        (error, json) => {
          if (error) {
            context.commit('fillErrors', error);
            console.log('Error: ', error);
            context.commit('removeInProgress');

          } else {
            context.commit('login', {json: json, router: payload.router});
            context.commit('removeInProgress');
          }
        }
      );
    },

    signin: (context, payload) => {
      context.commit('setInProgress');

      ApiClient('/sessions.json',
        context,
        payload.router,
        {
          method: 'POST',
          body:
            JSON.stringify({
              signin:
                {
                  email: payload.body.email,
                  password: payload.body.password,
                }
            })
        },
        (error, json) => {
          if (error) {
            context.commit('removeInProgress');
            context.commit('fillErrors', error);
            console.log('Error: ', error);
          } else {
            context.commit('removeInProgress');
            context.commit('login', {json: json, router: payload.router});
          }
        }
      );
    },

    signout: (context, payload) => {
      ApiClient('/sessions/signout',
        context,
        payload.router,
        {
          method: 'DELETE',
          headers: context.getters.authHeader
        },
        (error, json) => {
          if (error) {
            console.log('Error: ', error);
          } else {
            context.commit('signout', payload)
          }
        })
    },

    selectCategory: (context, payload) => {
      context.commit('clearCategory');
      context.commit('setInProgress');

      ApiClient(`/categories/${payload.id}.json`,
        context,
        payload.router,
        {
          headers: context.getters.authHeader
        },
        (error, json) => {
          if (error) {
            console.log('Error: ', error);
            router.push({name: 'Categories'});
          } else {
            context.commit('selectCategory',
              {
                id: payload.id,
                ...json
              }
            );

            context.commit('removeInProgress');
          }
        });

    },

    startSession: (context, payload) => {
      context.commit('setInProgress');
      ApiClient('/learning_sessions.json',
        context,
        payload.router,
        {
          method: 'POST',
          headers: context.getters.authHeader,
          body: `{"category_id": ${context.state.selectedCategory.category_id}}`
        },
        (error, json) => {
          if (error) {
            console.log('Error: ', error);
          } else {
            context.commit('startSession', json)
            context.commit('removeInProgress');
          }
        }
      );
    },

    dontKnow: function (contex, payload) {
      contex.commit('dontKnow')
    },

    nextCard: (context, payload) => {
      context.commit('nextCard');
    },

    closeFinishDialog: (context, payload) => {
      context.commit('closeFinishDialog');
    },

    clickStar: (context, payload) => {
      context.commit('clickStar', payload)
    },

    finishSession: function (context, payload) {
      context.commit('finishSession');
    },

    cancelSession: function (context, payload) {
      context.commit('returnToCategory')
    },

    returnToCategory: function (context, payload) {
      let id = context.state.session.details.id;
      let correct_answers = context.state.session.questions.reduce((acc, question) => {
        if (question.state === 'correct') {
          return acc += 1
        } else {
          return acc
        }
      }, 0);

      let wrong_answers = context.state.session.questions.length - correct_answers;

      let flash_cards = context.state.session.questions.map((question) => {
        return {id: question.id, state: question.state === 'correct'}
      });

      ApiClient(`/learning_sessions/${id}.json`,
        context,
        payload.router,
        {
          headers: context.getters.authHeader,
          method: 'PUT',
          body: `{
            "learning_session_detail": {
              "correct_answers": ${correct_answers},
              "wrong_answers": ${wrong_answers},
              "flash_cards": ${JSON.stringify(flash_cards)}
            }
          }`
        },
        (error, json) => {
          if (error) {
            console.log('Error: ', error);
          }
        }
      );
      context.commit('returnToCategory')
    },

    createCategory: function (context, payload) {
      context.commit('setInProgress');

      ApiClient('/categories',
        context,
        payload.router,
        {
          headers: context.getters.authHeader,
          method: 'POST',
          body: JSON.stringify({
            category:
              {
                name: payload.name
              }
          })
        },
        (error, json) => {
          if (error) {
            console.log('Error: ', error);
          } else {
            context.commit('removeInProgress');
            context.commit('addCategories', json);
            context.commit('removeInEdit');
          }
        }
      )
    },

    setCategories: function (context, payload) {
      context.commit('setInProgress');
      ApiClient('/categories.json',
        context,
        payload.router,
        {
          headers: context.getters.authHeader
        },
        (error, json) => {
          if (error) {
            console.log('Error: ', error);
          } else {
            context.commit('setCategories', json)
            context.commit('removeInProgress');
          }
        });
    },

    createCard: function (context, payload) {
      context.commit('setInProgress');

      ApiClient('/flash_cards',
        context,
        payload.router,
        {
          headers: context.getters.authHeader,
          method: 'POST',
          body: JSON.stringify({
            flash_card:
              {
                face: payload.face,
                back: payload.back,
                category_id: context.state.selectedCategory.category_id
              }
          })
        },
        (error, json) => {
          if (error) {
            console.log('Error: ', error);
          } else {
            context.commit('removeInProgress');
            context.commit('addCard', json);
            context.commit('removeInEdit');
          }
        }
      )
    },

    deleteCard: function (context, payload) {
      context.commit('deleteCard', payload.id);
      ApiClient(`/flash_cards/${payload.id}.json`,
        context,
        payload.router,
        {
          headers: context.getters.authHeader,
          method: 'DELETE'

        },
        (error, json) => {
          if (error) {
            console.log('Error: ', error);
          }
        }
      )
    },

    sortCards: function (context, payload) {
      context.commit('sortCards');
    }
  }
});

store.subscribe((mutation, state) => {
  new LocalStorageService(state).setStore();
});



