<template>
  <svg width="30" height="30" viewBox="-4 -4 60 60" xmlns="http://www.w3.org/2000/svg" version="1.1">
    <g id="layer1"
       transform="translate(0,-244.08332)">
      <circle
        style="opacity:1;fill:none;fill-opacity:1;fill-rule:nonzero;stroke:#666666;stroke-width:3;stroke-miterlimit:4;stroke-dasharray:none;stroke-opacity:1"
        id="path844"
        cx="26.621695"
        cy="270.67059"
        r="24.867708"/>
      <path
        style="opacity:1;fill:#bbbbbb;fill-opacity:1;fill-rule:nonzero;stroke:#666666;stroke-width:3;stroke-miterlimit:4;stroke-dasharray:none;stroke-opacity:1"
        v-if=show_2_4
        id="path817"
        type="arc"
        cx="26.621695"
        cy="270.67059"
        rx="24.867708"
        ry="24.867708"
        start="0"
        end="1.5707963"
        d="M 51.489403,270.67059 A 24.867708,24.867708 0 0 1 26.621695,295.5383 l 0,-24.86771 z"/>
      <path
        id="path819"
        style="opacity:1;fill:#aaaaaa;fill-opacity:1;fill-rule:nonzero;stroke:#666666;stroke-width:3;stroke-miterlimit:4;stroke-dasharray:none;stroke-opacity:1"
        v-if=show_3_4
        type="arc"
        cx="26.621695"
        cy="270.67059"
        rx="24.867708"
        ry="24.867708"
        start="1.5707963"
        end="3.1415927"
        d="M 26.621695,295.5383 A 24.867708,24.867708 0 0 1 9.0375693,288.25472 24.867708,24.867708 0 0 1 1.7539864,270.67059 l 24.8677086,0 z"/>
      <path
        style="opacity:1;fill:#999999;fill-opacity:1;fill-rule:nonzero;stroke:#666666;stroke-width:3;stroke-miterlimit:4;stroke-dasharray:none;stroke-opacity:1"
        v-if=show_4_4
        id="path821"
        type="arc"
        cx="26.621695"
        cy="270.67059"
        rx="24.867708"
        ry="24.867708"
        start="3.1415927"
        end="4.712389"
        d="m 1.7539864,270.67059 a 24.867708,24.867708 0 0 1 24.8677086,-24.8677 l 0,24.8677 z"/>
      <path
        d="m 26.621695,245.80289 a 24.867708,24.867708 0 0 1 24.867708,24.8677 H 26.621695 Z"
        end="0"
        start="4.712389"
        ry="24.867708"
        rx="24.867708"
        cy="270.67059"
        cx="26.621695"
        type="arc"
        id="path823"
        v-if=show_1_4
        style="opacity:1;fill:#cccccc;fill-opacity:1;fill-rule:nonzero;stroke:#666666;stroke-width:3;stroke-miterlimit:4;stroke-dasharray:none;stroke-opacity:1"/>
    </g>
  </svg>
</template>

<script>
  export default {
    name: 'CircleSvg',
    props: ['box'],

    computed: {
      show_1_4: function () {
        return this.box >= 1
      },
      show_2_4: function () {
        return this.box >= 2
      },
      show_3_4: function () {
        return this.box >= 3
      },
      show_4_4: function () {
        return this.box === 4
      }
    }
  }
</script>



