<template>
  <section class="section login">
    <div class="container">
      <div class="columns">
        <div class="column is-half is-offset-one-quarter">
          <div class="card">
            <div class="card-content">

              <Loading v-show="isInProgress" class="spinner" />

              <h1 class="title">
                Join Us!
              </h1>

              <form action="">
                <div class="field">
                  <label class="label">How do you want us to call you?</label>
                  <div class="control has-icons-right">
                    <input v-bind:class="{'input': true, 'is-danger': errors.name}"
                           v-model="registration.name"
                           type="text"
                           placeholder="Your name"
                           v-on:keyup="cleanErrorKey('name')"
                    >
                    <span class="icon is-small is-right" v-if="errors.name">
                      <i class="fa fa-warning"></i>
                    </span>
                  </div>
                  <div class="help is-danger" v-if="errors.name">{{errors.name[0]}}</div>
                </div>

                <div class="field">
                  <label class="label">Email</label>
                  <div class="control has-icons-left has-icons-right">
                    <input v-bind:class="{'input': true, 'is-danger': errors.email}"
                           v-model="registration.email"
                           type="email"
                           placeholder="example@mail.com"
                           v-on:keyup="cleanErrorKey('email')"
                    >
                    <span class="icon is-small is-left">
                      <i class="fa fa-envelope"></i>
                    </span>

                    <span class="icon is-small is-right" v-if="errors.email">
                      <i class="fa fa-warning"></i>
                    </span>
                  </div>
                  <div class="help is-danger" v-if="errors.email">{{errors.email[0]}}</div>
                </div>

                <div class="field">
                  <label class="label">Password</label>
                  <div class="control has-icons-right">
                    <input v-bind:class="{'input': true, 'is-danger': errors.encrypted_password}"
                           v-model="registration.password"
                           type="password"
                           placeholder="password"
                           v-on:keyup="cleanErrorKey('encrypted_password')"
                    >
                    <span class="icon is-small is-right" v-if="errors.encrypted_password">
                      <i class="fa fa-warning"></i>
                    </span>
                  </div>
                  <div class="help is-danger" v-if="errors.encrypted_password">{{errors.encrypted_password[0]}}
                  </div>
                </div>

                <div class="field is-grouped is-grouped-centered">
                  <p class="control">
                    <button type="button" v-on:click.prevent="signin"
                            class="button is-primary">
                      Sign Up
                    </button>
                  </p>
                </div>

              </form>

            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import Loading from "./Loading.vue";

  export default {
    name: 'Registration',

    components: {
      'Loading': Loading
    },

    data: function () {
      return {
        registration: {
          name: "",
          email: "",
          password: "",
        }
      }
    },

    methods: {
      signin: function () {
        if (!this.isInProgress) {
          this.$store.dispatch('signup', {body: this.registration, router: this.$router});
        }
      },

      cleanErrorKey: function (key) {
        if (this.errors[key]) {
          this.$store.dispatch("cleanErrorKey", {key: key, router: this.$router})
        }
      }
    },

    computed: {
      errors: function () {
        return this.$store.state.errors
      },

      isInProgress: function () {
        return this.$store.state.isInProgress;
      }
    }
  }
</script>

<style lang="sass" scoped>
  @import "../values.sass"

  .spinner
    top: 45%
    left: 45%
  .title
    @include medium_bold_font

  +mobile
    .login
      padding-top: 1.5em
</style>



