<template>
  <div class="spinner">
    <span class="fa fa-spinner"></span>
  </div>
</template>

<script>
    export default {
        name: "Loading"
    }
</script>

<style lang="sass" scoped>
  @import "../values.sass"
  @keyframes rotate
    0%
      transform: rotateZ(0deg)
    100%
      transform: rotateZ(360deg)

  .spinner
    animation: rotate 0.9s linear infinite
    position: absolute
    font-size: 40px
    z-index: 3
    color: $light_gray
</style>



